.App {
  text-align: center;
  /* background-color: rgba(48, 84, 126, 0.61); */
  background: rgb(0,219,255);
  background: radial-gradient(circle, rgba(0,219,255,1) 0%, rgba(9,70,121,1) 100%);
}



a {
  color: rgb(178, 229, 241);
}

 .border{
   display: flex;
   flex-direction: column;
   padding: 50px 50px;
   /* background-color: rgba(48, 84, 126, 0.61); */
   align-items: center;
 }

 button{
   width: 150px;
   border-radius: 10px;
 }

 .tip{
  cursor: pointer;
  position: absolute;
  display: inline-block;
  transform: translate(10px, -5px);
   color: bisque;
   background-color: #4e5258ad;
   border-radius: 4px ;
   padding: 0 5px;
 }


.tipper{
  cursor: pointer;
   position: absolute;
   display: inline-block;
  transform: translate(40px, -8px);
  color: bisque;
  background-color: #4e5258ad;
  border-radius: 4px;
  padding: 5px 15px 7px ;
  max-width: 250px;
}
.tipper2{
  transform: translate(55px, -8px);
}

#crackBtn{
  background-color: rgba(255, 0, 0, 0.616);
  color: bisque;
}

 @media (max-width:950px){

  .tipper{
    cursor: pointer;
    position: relative;
    display: block;
    transform: inherit;
    color: bisque;
    background-color: #4e5258ad;
    border-radius: 4px;
    padding: 5px 15px 7px ;
    max-width: 250px;
    min-width: 230px;
    margin-top: 5px;
  }
  .tipper2{
    /* transform: translate(-172px, 25px); */
    margin-top: 5px;
    position: relative;
    transform: inherit;
    display: block;
    /* background-color: #4e5258; */
    min-width: 250px;
  }
.tip{
  margin-right: 200px;
}

 }