}

.App {
  text-align: center;
  /* background-color: rgba(48, 84, 126, 0.61); */
  background: rgb(0,219,255);
  background: radial-gradient(circle, rgba(0,219,255,1) 0%, rgba(9,70,121,1) 100%);
}



a {
  color: rgb(178, 229, 241);
}

 .border{
   display: flex;
   flex-direction: column;
   padding: 50px 50px;
   /* background-color: rgba(48, 84, 126, 0.61); */
   align-items: center;
 }

 button{
   width: 150px;
   border-radius: 10px;
 }

 .tip{
  cursor: pointer;
  position: absolute;
  display: inline-block;
  -webkit-transform: translate(10px, -5px);
          transform: translate(10px, -5px);
   color: bisque;
   background-color: #4e5258ad;
   border-radius: 4px ;
   padding: 0 5px;
 }


.tipper{
  cursor: pointer;
   position: absolute;
   display: inline-block;
  -webkit-transform: translate(40px, -8px);
          transform: translate(40px, -8px);
  color: bisque;
  background-color: #4e5258ad;
  border-radius: 4px;
  padding: 5px 15px 7px ;
  max-width: 250px;
}
.tipper2{
  -webkit-transform: translate(55px, -8px);
          transform: translate(55px, -8px);
}

#crackBtn{
  background-color: rgba(255, 0, 0, 0.616);
  color: bisque;
}

 @media (max-width:950px){

  .tipper{
    cursor: pointer;
    position: relative;
    display: block;
    -webkit-transform: inherit;
            transform: inherit;
    color: bisque;
    background-color: #4e5258ad;
    border-radius: 4px;
    padding: 5px 15px 7px ;
    max-width: 250px;
    min-width: 230px;
    margin-top: 5px;
  }
  .tipper2{
    /* transform: translate(-172px, 25px); */
    margin-top: 5px;
    position: relative;
    -webkit-transform: inherit;
            transform: inherit;
    display: block;
    /* background-color: #4e5258; */
    min-width: 250px;
  }
.tip{
  margin-right: 200px;
}

 }

.slide-enter {
    opacity: 0;
    /* margin-left: -100%; */
    -webkit-transform: translateY(-30vh);
            transform: translateY(-30vh);
  }
  .slide-enter-active {
    opacity: 1;
    /* margin-left: 0; */
    -webkit-transform: translateY(0vh);
            transform: translateY(0vh);
    transition: 1500ms ease-in-out;
  }
  .slide-exit {
    opacity: 1;
    /* margin-left: 0; */
  }
  .slide-exit-active {
    opacity: 1;
    /* transform: translateX(10vw); */
    transition: 500ms ease-in; 
  }
  
  /* .custom {
    color: rgba(5, 41, 71, 0.726);
  } */
  


  .slideUp-enter {
    opacity: 0;
    background: rgba(214, 162, 98, 0.075);
    /* margin-left: -100%; */
    -webkit-transform: translateY(20vh);
            transform: translateY(20vh);
  }
  .slideUp-enter-active {
    opacity: 1;
    /* margin-left: 0; */
    -webkit-transform: translateY(0);
            transform: translateY(0);
    transition: 1500ms ease-in-out;
  }
  .slideUp-exit {
    opacity: 1;
    /* margin-left: 0; */
  }
  .slideUp-exit-active {
    opacity: 1;
    -webkit-transform: translateX(10vw);
            transform: translateX(10vw);
    transition: 500ms ease-in; 
  }
