
.slide-enter {
    opacity: 0;
    /* margin-left: -100%; */
    transform: translateY(-30vh);
  }
  .slide-enter-active {
    opacity: 1;
    /* margin-left: 0; */
    transform: translateY(0vh);
    transition: 1500ms ease-in-out;
  }
  .slide-exit {
    opacity: 1;
    /* margin-left: 0; */
  }
  .slide-exit-active {
    opacity: 1;
    /* transform: translateX(10vw); */
    transition: 500ms ease-in; 
  }
  
  /* .custom {
    color: rgba(5, 41, 71, 0.726);
  } */
  


  .slideUp-enter {
    opacity: 0;
    background: rgba(214, 162, 98, 0.075);
    /* margin-left: -100%; */
    transform: translateY(20vh);
  }
  .slideUp-enter-active {
    opacity: 1;
    /* margin-left: 0; */
    transform: translateY(0);
    transition: 1500ms ease-in-out;
  }
  .slideUp-exit {
    opacity: 1;
    /* margin-left: 0; */
  }
  .slideUp-exit-active {
    opacity: 1;
    transform: translateX(10vw);
    transition: 500ms ease-in; 
  }